import React from 'react'
import './SearchPage.css'
import { useStateValue } from './StateProvider'
import useGoogleSearch from './useGoogleSearch'
import Response from "./response";
import { Link } from "react-router-dom";
import Search from "./pages/Search";
import SearchIcon from "@material-ui/icons/Search"
import Description from "@material-ui/icons/Description"
import ImageIcon from "@material-ui/icons/Image"
import LocalOfferIcon from "@material-ui/icons/LocalOffer"
import RoomIcon from "@material-ui/icons/Room";
import MoreVertIcon from "@material-ui/icons/MoreVert"

function SearchPage() {
  const [{ term='tesla' }, dispatch] = useStateValue();

  // Live APR Call
  const {data} = useGoogleSearch(term);

  // Mock API Call
  //const data = Response;

  console.log(data);

  return (
    <div className="searchPage">
      <div className="searchPage__header">
        <Link to="/">
          <img 
            className="searchPage__logo"
            src="data:image/svg+xml;base64,"
            alt=""
          />
        </Link>
        <div className="searchPage__headerBody">
          <Search hideButtons/>
          <div className="searchPage__options">
            <div className="searchPage__optionsLeft">
              <div className="searchPage__option">
                <SearchIcon />
                <Link to="/">All</Link>
              </div>
              <div className="searchPage__option">
                <Description />
                <Link to="/news">News</Link>
              </div>
              <div className="searchPage__option">
                <ImageIcon />
                <Link to="/images">Images</Link>
              </div>
              <div className="searchPage__option">
                <LocalOfferIcon />
                <Link to="/shopping">shopping</Link>
              </div>
              <div className="searchPage__option">
                <RoomIcon />
                <Link to="/maps">maps</Link>
              </div>
              <div className="searchPage__option">
                <MoreVertIcon />
                <Link to="/more">more</Link>
              </div>
            </div>
            <div className="searchPage__optionsRight">
              <div className="searchPape__option">
                <Link to="/settings">Settings</Link>
              </div>
              <div className="searchPape__option">
                <Link to="/tools">Tools</Link>
              </div>
            </div>
          </div>
        </div>
        
      </div>

      { term && (
        <div className="searchPage__results">
          <p className="searchPage__resultConunt">
            About 
            {data?.searchInformation.formattedTotalResults}
            results 
            ({data?.searchInformation.formattedSearchTime} seconds) for {term}
          </p>
          {
            data?.items.map(item => (
              <div className="searchPage__result">
                <a className="searchPage__resultImg" href={item.link} target="_blank">
                  {item.pagemap?.cse_image?.length > 0 &&
                    item.pagemap?.cse_image[0]?.src &&(
                      <img 
                        className="searchPage__resultImage"
                        src={
                          item.pagemap?.
                          cse_image?.length>0 &&
                          item.pagemap?.
                          cse_image[0]?.src
                        }
                      />
                    )
                  }
                  {item.displayLink}
                </a>
                <a className="searchPage__resultTitle" href={item.link} target="_blank">
                  <h2>{item.title}</h2>
                </a>
                <p className="searchPage__resultSnippet">
                  {item.snippet}
                </p>
              </div>
            ))
          }
        </div>
      )}
    </div>
  )
}

export default SearchPage
